import { motion } from "framer-motion";
import { Globe } from "lucide-react";
import { useLanguage, Language } from "../contexts/LanguageContext";
import { useState, useEffect } from "react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";

export function LanguageSwitcher() {
  const { language, setLanguage } = useLanguage();
  const [isOpen, setIsOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);
  const [isSafariBrowser, setIsSafariBrowser] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
    // Detect Safari
    const ua = navigator.userAgent;
    setIsSafariBrowser(/^((?!chrome|android).)*safari/i.test(ua));
  }, []);

  const languages: { code: Language; label: string; flag: string }[] = [
    { code: "zh", label: "", flag: "" },
    { code: "en", label: "English", flag: "" },
    { code: "fr", label: "Franais", flag: "" },
    { code: "de", label: "Deutsch", flag: "" },
    { code: "hi", label: "", flag: "" },
    { code: "pt", label: "Portugus", flag: "" },
    { code: "ko", label: "", flag: "" },
  ];

  const currentLang = languages.find((l) => l.code === language);

  const MotionButton = isSafariBrowser ? 'button' : motion.button;
  const MotionDiv = isSafariBrowser ? 'div' : motion.div;

  return (
    <div className="relative">
      {/* Toggle Button */}
      <MotionButton
        {...(isSafariBrowser ? {} : {
          whileHover: !isMobile && !reducedMotion ? { scale: 1.05 } : {},
          whileTap: !isMobile && !reducedMotion ? { scale: 0.95 } : {}
        })}
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center gap-2 px-4 py-2 rounded-xl bg-slate-800/50 border border-slate-700/50 hover:bg-slate-700/50 hover:border-cyan-400/30 transition-all duration-300 group"
      >
        <Globe className="w-4 h-4 text-cyan-400 group-hover:rotate-12 transition-transform duration-300" />
        <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
          {currentLang?.label}
        </span>
      </MotionButton>

      {/* Dropdown Menu */}
      <MotionDiv
        {...(isSafariBrowser ? {
          style: {
            opacity: isOpen ? 1 : 0,
            pointerEvents: isOpen ? "auto" : "none"
          } as any
        } : {
          initial: { opacity: 0, y: -10, scale: 0.95 },
          animate: {
            opacity: isOpen ? 1 : 0,
            y: isOpen ? 0 : -10,
            scale: isOpen ? 1 : 0.95,
            pointerEvents: isOpen ? "auto" : "none",
          },
          transition: { duration: 0.2, ease: "easeOut" }
        })}
        className="absolute top-full right-0 mt-2 w-48 max-h-80 overflow-y-auto rounded-xl bg-slate-900/95 backdrop-blur-2xl border border-slate-700/50 shadow-2xl shadow-cyan-500/10 scrollbar-thin scrollbar-thumb-cyan-500/50 scrollbar-track-slate-800/50"
      >
        {languages.map((lang, index) => (
          <MotionButton
            key={lang.code}
            {...(isSafariBrowser ? {
              style: {
                opacity: isOpen ? 1 : 0
              } as any
            } : {
              initial: { opacity: 0, x: -10 },
              animate: {
                opacity: isOpen ? 1 : 0,
                x: isOpen ? 0 : -10,
              },
              transition: { delay: (isMobile || reducedMotion) ? 0 : index * 0.05, duration: 0.2 },
              whileHover: !isMobile && !reducedMotion ? { backgroundColor: "rgba(6, 182, 212, 0.1)" } : {}
            })}
            onClick={() => {
              setLanguage(lang.code);
              setIsOpen(false);
            }}
            className={`w-full flex items-center gap-3 px-4 py-3 text-left transition-colors ${
              language === lang.code
                ? "bg-cyan-500/10 text-cyan-400"
                : "text-slate-300 hover:text-white"
            }`}
          >
            <span className="text-xl">{lang.flag}</span>
            <span className="text-sm">{lang.label}</span>
            {language === lang.code && (
              isSafariBrowser ? (
                <div className="ml-auto w-1.5 h-1.5 rounded-full bg-cyan-400" />
              ) : (
                <motion.div
                  layoutId="activeLanguage"
                  className="ml-auto w-1.5 h-1.5 rounded-full bg-cyan-400"
                  transition={{ duration: 0.3 }}
                />
              )
            )}
          </MotionButton>
        ))}

        {/* Accent Line */}
        <div className="absolute bottom-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-cyan-500 to-transparent opacity-50" />
      </MotionDiv>

      {/* Click Outside to Close */}
      {isOpen && (
        <div
          className="fixed inset-0 z-[-1]"
          onClick={() => setIsOpen(false)}
        />
      )}
    </div>
  );
}
