import { ModernHero } from "./components/ModernHero";
import { FullWidthNav } from "./components/FullWidthNav";
import { ScrollToTop } from "./components/ScrollToTop";
import { SEO } from "./components/SEO";
import { useEffect, lazy, Suspense } from "react";

// Lazy load non-critical components for code splitting
const ProblemSolution = lazy(() => import("./components/ProblemSolution"));
const BentoFeatures = lazy(() => import("./components/BentoFeatures"));
const InteractiveDemo = lazy(() => import("./components/InteractiveDemo"));
const MagneticCTA = lazy(() => import("./components/MagneticCTA"));
const ModernFooter = lazy(() => import("./components/ModernFooter"));

export default function App() {
  useEffect(() => {
    // Load Google Fonts
    const link = document.createElement("link");
    link.href = "https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800&display=swap";
    link.rel = "stylesheet";
    document.head.appendChild(link);

    // Smooth scroll behavior
    document.documentElement.style.scrollBehavior = "smooth";

    // Dynamically load Firebase (prevents blocking when ad blockers intercept the module)
    import("./src/firebase")
      .then(({ initializeErrorTracking, trackPageLoad, logPageView }) => {
        try {
          initializeErrorTracking();
          trackPageLoad();
          logPageView('Home', window.location.href);
          console.log(' Firebase services initialized');
        } catch (error) {
          console.warn(' Firebase initialization failed:', error);
        }
      })
      .catch((error) => {
        console.warn(' Firebase module blocked (ad blocker detected):', error);
      });

    return () => {
      document.documentElement.style.scrollBehavior = "auto";
    };
  }, []);

  return (
    <div className="min-h-screen bg-slate-950 overflow-x-hidden">
      <SEO />
      <FullWidthNav />
      <ModernHero />

      {/* Lazy loaded components with Suspense */}
      <Suspense fallback={<div className="min-h-screen" />}>
        <ProblemSolution />
      </Suspense>

      <Suspense fallback={<div className="min-h-screen" />}>
        <BentoFeatures />
      </Suspense>

      <Suspense fallback={<div className="min-h-screen" />}>
        <InteractiveDemo />
      </Suspense>

      <Suspense fallback={<div className="min-h-screen" />}>
        <MagneticCTA />
      </Suspense>

      <Suspense fallback={<div className="min-h-screen" />}>
        <ModernFooter />
      </Suspense>

      <ScrollToTop />
    </div>
  );
}