import { motion, HTMLMotionProps } from "framer-motion";
import { ReactNode, useState, useEffect } from "react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";

// Utility function for class names
function cn(...classes: (string | undefined | null | false)[]) {
  return classes.filter(Boolean).join(' ');
}

interface ModernButtonProps extends Omit<HTMLMotionProps<"button">, "size"> {
  variant?: "primary" | "secondary" | "ghost" | "outline";
  size?: "sm" | "md" | "lg";
  children: ReactNode;
  icon?: ReactNode;
  glow?: boolean;
  magnetic?: boolean;
}

export function ModernButton({
  variant = "primary",
  size = "md",
  children,
  icon,
  glow = false,
  magnetic = false,
  className,
  ...props
}: ModernButtonProps) {
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);
  const [isSafariBrowser, setIsSafariBrowser] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
    // Detect Safari
    const ua = navigator.userAgent;
    setIsSafariBrowser(/^((?!chrome|android).)*safari/i.test(ua));
  }, []);

  const baseStyles = "relative inline-flex items-center justify-center gap-2 font-medium disabled:opacity-50 disabled:cursor-not-allowed overflow-hidden group";

  // Safari-specific variants without hover animations
  const safariVariants = {
    primary: `
      bg-gradient-to-r from-cyan-500 via-blue-600 to-blue-700
      text-white rounded-full
      border border-cyan-400/20
    `,
    secondary: `
      bg-gradient-to-r from-slate-700 to-slate-800
      text-white rounded-full
      border border-slate-600/50
    `,
    ghost: `
      bg-transparent text-cyan-400 rounded-full
      border border-cyan-400/30
    `,
    outline: `
      bg-slate-900/50 text-white rounded-full
      border-2 border-cyan-400/40
      backdrop-blur-sm
    `,
  };

  const variants = {
    primary: `
      bg-gradient-to-r from-cyan-500 via-blue-600 to-blue-700
      text-white rounded-full
      transition-all duration-300
      hover:shadow-lg hover:shadow-cyan-500/50
      hover:scale-105 active:scale-95
      border border-cyan-400/20
      before:absolute before:inset-0
      before:bg-gradient-to-r before:from-white/0 before:via-white/20 before:to-white/0
      before:translate-x-[-100%] hover:before:translate-x-[100%]
      before:transition-transform before:duration-700
    `,
    secondary: `
      bg-gradient-to-r from-slate-700 to-slate-800
      text-white rounded-full
      transition-all duration-300
      hover:from-slate-600 hover:to-slate-700
      hover:shadow-lg hover:shadow-slate-500/30
      hover:scale-105 active:scale-95
      border border-slate-600/50
    `,
    ghost: `
      bg-transparent text-cyan-400 rounded-full
      transition-all duration-300
      hover:bg-slate-800/50
      border border-cyan-400/30
      hover:border-cyan-400/60
      hover:shadow-lg hover:shadow-cyan-500/20
    `,
    outline: `
      bg-slate-900/50 text-white rounded-full
      border-2 border-cyan-400/40
      transition-all duration-300
      hover:bg-cyan-400/10 hover:border-cyan-400/80
      hover:shadow-lg hover:shadow-cyan-500/30
      backdrop-blur-sm
    `,
  };

  // Use Safari variants on Safari to avoid hover animation lag
  const activeVariants = isSafariBrowser ? safariVariants : variants;

  const sizes = {
    sm: "px-4 py-2 text-sm h-9",
    md: "px-6 py-3 text-base h-11",
    lg: "px-8 py-4 text-lg h-14",
  };

  const glowVariant = variant === "primary" ? "cyan" : "slate";

  return isSafariBrowser ? (
    <button
      className={cn(
        baseStyles,
        activeVariants[variant],
        sizes[size],
        className
      )}
      {...(props as any)}
    >
      {/* Glow effect - Disabled on Safari */}

      {/* Icon - No rotation on Safari */}
      {icon && (
        <span>
          {icon}
        </span>
      )}

      {/* Text */}
      <span className="relative z-10">{children}</span>

    </button>
  ) : (
    <motion.button
      whileHover={magnetic && !isMobile && !reducedMotion ? { scale: 1.05 } : {}}
      whileTap={!isMobile && !reducedMotion ? { scale: 0.95 } : {}}
      className={cn(
        baseStyles,
        activeVariants[variant],
        sizes[size],
        className
      )}
      {...props}
    >
      {/* Glow effect */}
      {glow && (
        <div className={cn(
          "absolute inset-0 rounded-full blur-xl opacity-0 group-hover:opacity-70 transition-opacity duration-500 -z-10",
          glowVariant === "cyan" ? "bg-cyan-500/50" : "bg-slate-500/50"
        )} />
      )}

      {/* Icon */}
      {icon && (
        <span className="group-hover:rotate-12 transition-transform duration-300">
          {icon}
        </span>
      )}

      {/* Text */}
      <span className="relative z-10">{children}</span>

      {/* Animated gradient overlay */}
      {!isMobile && !reducedMotion && (
        <motion.div
          className="absolute inset-0 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"
          style={{
            background: variant === "primary"
              ? "linear-gradient(45deg, transparent 30%, rgba(255,255,255,0.1) 50%, transparent 70%)"
              : "linear-gradient(45deg, transparent 30%, rgba(0,204,255,0.1) 50%, transparent 70%)",
            backgroundSize: "200% 100%",
          }}
          animate={{
            backgroundPosition: ["0% 0%", "200% 0%"],
          }}
          transition={{
            duration: 2,
            repeat: Infinity,
            ease: "linear",
          }}
        />
      )}
    </motion.button>
  );
}
