import { motion, useScroll, useTransform } from "framer-motion";
import { Download, ChevronDown } from "lucide-react";
import { ModernButton } from "./ModernButton";
import { useRef, useState, useEffect } from "react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";
import { useLanguage } from "../contexts/LanguageContext";
import { getTranslation } from "../translations";

export function ModernHero() {
  const { language } = useLanguage();
  const t = getTranslation(language);
  const containerRef = useRef<HTMLDivElement>(null);
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);
  const [isSafariBrowser, setIsSafariBrowser] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
    // Detect Safari
    const ua = navigator.userAgent;
    setIsSafariBrowser(/^((?!chrome|android).)*safari/i.test(ua));
  }, []);

  // Conditional components - use plain HTML on Safari to avoid Framer Motion engine
  const MotionDiv = isSafariBrowser ? 'div' : motion.div;
  const MotionH1 = isSafariBrowser ? 'h1' : motion.h1;
  const MotionP = isSafariBrowser ? 'p' : motion.p;

  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end start"],
  });

  const y = useTransform(scrollYProgress, [0, 1], ["0%", "50%"]);
  const opacity = useTransform(scrollYProgress, [0, 0.5], [1, 0]);

  // Disable animations on Safari for performance
  const animationDuration = isSafariBrowser ? 0 : (isMobile || reducedMotion ? 0.4 : 0.8);

  const handleGooglePlayClick = () => {
    // Dynamically load Firebase to prevent ad blocker issues
    import("../src/firebase")
      .then(({ logDownload }) => {
        try {
          logDownload('google_play', 'ViewFlow Browser');
        } catch (error) {
          console.warn('Analytics blocked:', error);
        }
      })
      .catch((error) => {
        console.warn('Firebase module blocked:', error);
      });
  };

  return (
    <div ref={containerRef} className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Animated Grid Background */}
      <div className="absolute inset-0 opacity-30">
        <div
          className="absolute inset-0"
          style={{
            backgroundImage: `
              linear-gradient(to right, #00ccff22 1px, transparent 1px),
              linear-gradient(to bottom, #00ccff22 1px, transparent 1px)
            `,
            backgroundSize: "80px 80px",
          }}
        />
      </div>

      {/* Gradient Orbs - Removed completely on Safari for performance */}
      {!isMobile && !reducedMotion && !isSafariBrowser && (
        <>
          <motion.div
            className="absolute top-1/4 -left-1/4 w-[600px] h-[600px] bg-blue-600/30 rounded-full blur-[120px]"
            animate={{
              scale: [1, 1.2, 1],
              x: [0, 50, 0],
              y: [0, 30, 0],
            }}
            transition={{
              duration: 8,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
          <motion.div
            className="absolute bottom-1/4 -right-1/4 w-[500px] h-[500px] bg-cyan-400/20 rounded-full blur-[120px]"
            animate={{
              scale: [1, 1.3, 1],
              x: [0, -30, 0],
              y: [0, 50, 0],
            }}
            transition={{
              duration: 10,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        </>
      )}
      {/* Static gradient orbs for mobile - Safari has no orbs for best performance */}
      {isMobile && !reducedMotion && !isSafariBrowser && (
        <>
          <div className="absolute top-1/4 -left-1/4 w-[600px] h-[600px] bg-blue-600/20 rounded-full blur-[120px]" />
          <div className="absolute bottom-1/4 -right-1/4 w-[500px] h-[500px] bg-cyan-400/15 rounded-full blur-[120px]" />
        </>
      )}

      <MotionDiv
        {...(isSafariBrowser ? {} : { style: { y, opacity } as any })}
        className="relative z-10 container mx-auto px-6 text-center"
      >
        {/* Main Title */}
        <MotionH1
          {...(isSafariBrowser ? {} : {
            initial: { opacity: 0, y: 30 },
            animate: { opacity: 1, y: 0 },
            transition: { duration: animationDuration, delay: 0.1 }
          })}
          className="text-6xl md:text-8xl font-bold mb-6 tracking-tight"
        >
          <span className="bg-gradient-to-r from-white via-cyan-200 to-cyan-400 bg-clip-text text-transparent">
            {t.hero.title}
          </span>
        </MotionH1>

        {/* Subtitle */}
        <MotionP
          {...(isSafariBrowser ? {} : {
            initial: { opacity: 0, y: 30 },
            animate: { opacity: 1, y: 0 },
            transition: { duration: animationDuration, delay: isMobile || reducedMotion ? 0.2 : 0.4 }
          })}
          className="text-xl md:text-3xl text-slate-300 mb-4 max-w-3xl mx-auto"
        >
          {t.hero.subtitle}
        </MotionP>

        <MotionP
          {...(isSafariBrowser ? {} : {
            initial: { opacity: 0, y: 30 },
            animate: { opacity: 1, y: 0 },
            transition: { duration: animationDuration, delay: isMobile || reducedMotion ? 0.3 : 0.5 }
          })}
          className="text-lg text-slate-400 mb-12 max-w-2xl mx-auto"
        >
          {t.hero.description || t.hero.subtitle}
        </MotionP>

        {/* CTA Buttons */}
        <MotionDiv
          {...(isSafariBrowser ? {} : {
            initial: { opacity: 0, y: 30 },
            animate: { opacity: 1, y: 0 },
            transition: { duration: animationDuration, delay: isMobile || reducedMotion ? 0.4 : 0.6 }
          })}
          className="flex flex-col sm:flex-row gap-4 justify-center items-center"
        >
          <a
            href="https://play.google.com/store/apps/details?id=com.viewflow.browser"
            target="_blank"
            rel="noopener noreferrer"
            onClick={handleGooglePlayClick}
          >
            <ModernButton
              size="lg"
              glow
              icon={<Download className="w-5 h-5" />}
            >
              {t.hero.getOnGoogleTV}
            </ModernButton>
          </a>

          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              alert(t.download.comingSoon);
            }}
          >
            <ModernButton
              size="lg"
              variant="outline"
              icon={<Download className="w-5 h-5" />}
            >
              {t.download.downloadAPK}
            </ModernButton>
          </a>
        </MotionDiv>

        {/* Stats - Commented out per requirements */}
        {/* <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.8 }}
          className="grid grid-cols-3 gap-8 max-w-2xl mx-auto mt-16"
        >
          {[
            { value: "500K+", label: "Downloads" },
            { value: "98%", label: "Satisfaction" },
            { value: "4.9", label: "Rating" },
          ].map((stat, index) => (
            <div key={index} className="text-center">
              <div className="text-3xl md:text-4xl font-bold text-cyan-400 mb-1">
                {stat.value}
              </div>
              <div className="text-sm text-slate-400">{stat.label}</div>
            </div>
          ))}
        </motion.div> */}
      </MotionDiv>

      {/* Scroll Indicator - Hidden on Safari for performance */}
      {!isMobile && !reducedMotion && !isSafariBrowser && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.8 }}
          className="absolute bottom-8 left-1/2 -translate-x-1/2"
        >
          <motion.div
            animate={{ y: [0, 10, 0] }}
            transition={{ duration: 2, repeat: Infinity }}
            className="flex flex-col items-center gap-2 text-slate-400"
          >
            <span className="text-sm">Scroll to explore</span>
            <ChevronDown className="w-5 h-5" />
          </motion.div>
        </motion.div>
      )}
    </div>
  );
}