import { motion, AnimatePresence } from "framer-motion";
import { ArrowUp } from "lucide-react";
import { useState, useEffect } from "react";
import { isMobileDevice, prefersReducedMotion } from "../src/utils/deviceDetect";

export function ScrollToTop() {
  const [isVisible, setIsVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [reducedMotion, setReducedMotion] = useState(false);
  const [isSafariBrowser, setIsSafariBrowser] = useState(false);

  useEffect(() => {
    setIsMobile(isMobileDevice());
    setReducedMotion(prefersReducedMotion());
    // Detect Safari
    const ua = navigator.userAgent;
    setIsSafariBrowser(/^((?!chrome|android).)*safari/i.test(ua));

    const toggleVisibility = () => {
      if (window.pageYOffset > 300) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    window.addEventListener("scroll", toggleVisibility);

    return () => {
      window.removeEventListener("scroll", toggleVisibility);
    };
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  const MotionButton = isSafariBrowser ? 'button' : motion.button;

  return (
    <AnimatePresence>
      {isVisible && (
        <MotionButton
          {...(isSafariBrowser ? {} : {
            initial: { opacity: 0, scale: 0, y: 100 },
            animate: { opacity: 1, scale: 1, y: 0 },
            exit: { opacity: 0, scale: 0, y: 100 },
            whileHover: !isMobile && !reducedMotion ? { scale: 1.1 } : {},
            whileTap: !isMobile && !reducedMotion ? { scale: 0.9 } : {}
          })}
          onClick={scrollToTop}
          className="fixed bottom-8 right-8 z-40 group"
          aria-label="Scroll to top"
        >
          {/* Glow effect */}
          <div className="absolute inset-0 bg-gradient-to-r from-blue-600 to-cyan-400 rounded-full blur-lg opacity-50 group-hover:opacity-75 transition-opacity"></div>
          
          {/* Button */}
          <div className="relative w-14 h-14 bg-gradient-to-br from-blue-600 to-cyan-400 rounded-full flex items-center justify-center shadow-lg shadow-cyan-500/50 border border-cyan-300/30">
            <ArrowUp className="w-6 h-6 text-white" />
          </div>

          {/* Orbiting ring - Disabled on Safari */}
          {!isMobile && !reducedMotion && !isSafariBrowser && (
            <motion.div
              className="absolute inset-0 rounded-full border-2 border-cyan-400/50"
              animate={{ rotate: 360 }}
              transition={{ duration: 4, repeat: Infinity, ease: "linear" }}
            />
          )}

          {/* Pulse effect - Disabled on Safari */}
          {!isMobile && !reducedMotion && !isSafariBrowser && (
            <motion.div
              className="absolute inset-0 rounded-full border-2 border-cyan-400"
              animate={{
                scale: [1, 1.5, 1.5],
                opacity: [1, 0, 0],
              }}
              transition={{
                duration: 2,
                repeat: Infinity,
                ease: "easeOut",
              }}
            />
          )}
        </MotionButton>
      )}
    </AnimatePresence>
  );
}
